<template><section><h1>DrawTools</h1>
<p>The <code v-pre>vc-handler-draw-point</code> component is used to draw point.
The <code v-pre>vc-handler-draw-polyline</code> component is used to draw polyline.
The <code v-pre>vc-handler-draw-polygon</code> component is used to draw polygon.</p>
<h2>Example</h2>
<h3>Draw Polyline</h3>
<h4>Preview</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready" scene3DOnly>
        <vc-handler-draw-point ref="handlerPoint" @activeEvt="activeEvt" @movingEvt="movingEvt" @drawEvt="drawEvt" :editable="editable"></vc-handler-draw-point>
        <vc-handler-draw-polyline :clampToGround="clampToGround" ref="handlerLine" @activeEvt="activeEvt" @movingEvt="movingEvt" @drawEvt="drawEvt" :editable="editable"></vc-handler-draw-polyline>
        <vc-handler-draw-polygon :clampToGround="clampToGround" ref="handlerPolygon" @activeEvt="activeEvt" @movingEvt="movingEvt" @drawEvt="drawEvt" :editable="editable"></vc-handler-draw-polygon>
        <vc-primitive-tileset :url="modelUrl" @readyPromise="readyPromise"></vc-primitive-tileset>
      </vc-viewer>
      <div class="demo-tool">
        <md-button class="md-raised md-accent" @click="toggle('handlerPoint')">{{ pointDrawing ? 'Stop' : 'Point' }}</md-button>
        <md-button class="md-raised md-accent" @click="toggle('handlerLine')">{{ polylineDrawing ? 'Stop' : 'Polyline' }}</md-button>
        <md-button class="md-raised md-accent" @click="toggle('handlerPolygon')">{{ polygonDrawing ? 'Stop' : 'Polygon' }}</md-button>
        <md-button class="md-raised md-accent" @click="clear">clear</md-button>
        <md-checkbox v-model="editable" class="md-primary">Editable</md-checkbox>
        <md-checkbox v-model="clampToGround" class="md-primary">ClampToGround(Polyline、Polygon)</md-checkbox>
      </div>
    </div>
  </template>
  
</doc-preview>
